.hire {
  text-transform: uppercase;
  margin-bottom: 48px;
  line-height: 28px;

  &__link {
    position: relative;
    font-size: 13px;
    font-weight: bold;
    text-decoration: none;
    letter-spacing: 3px;

    @include hover-after (opacity, 1);
  }

  &__link:after {
    @include content-after;
  }
}
